window.utils.$document.ready(() => {
  const select2 = $('.select2');

  select2.each((index, value) => {
    const $this = $(value);

    let options = $.extend({
      theme: 'bootstrap4',
      dropdownAutoWidth: true,
      width: '100%'
    }, $this.data('options'));

    if (options.preset == 'multi-input') {
      options = {
        ...options,
        tags: true,
        multiple: true,
        matcher: exactMatcher,
        containerCssClass: 'multi-input',
        dropdownCssClass: 'multi-input',
        allowClear: false,
        selectOnClose: true,
        tokenSeparators: ['|']
      };
    }

    if (options.preset == 'multi-select') {
      options = {
        ...options,
        multiple: true
      };
    }

    if (options.templateResult === 'deactivated-mark') {
      const formatState = function (state, element) {
        if (!state.id) {
          return state.text;
        }

        const optionData = $(state.element).data();
        if (optionData.deactivated) {
          return $(`<span>${state.text} <span class="text-warning">(${options.deactivatedLabel})</span></span>`);
        }

        return state.text;
      };

      options.templateResult = formatState;
      options.templateSelection = formatState;
    }

    if (options.data) {
      options.data = options.data
        .map(opt => ({
          selected: true,
          id: opt,
          text: opt
        }));
    }

    const $select2 = $this.select2(options);

    // Placeholder
    if (options.preset == 'multi-input') {
      updatePlaceholder($this, options);

      $this.on('select2:open select2:close select2:select select2:unselect select2:clear change change.select2', () => {
        setTimeout(() => {
          updatePlaceholder($this, options);
        });
      });
    }

    // Items
    if (options.items) {
      const items = options.items
        .map(_ => new Option(_.key, _.label, false, false));

      $select2.append(...items).trigger('change');
    }
  });

  function updatePlaceholder(elem, options) {
    const parent$ = $(elem).parent();

    const choices$ = $('.select2-selection__choice', parent$);
    const searchField$ = $('input.select2-search__field', parent$);

    const placeholder = options.placeholder.replace('[index]', choices$.length + 1);

    searchField$.prop('placeholder', placeholder);
  }

  function exactMatcher(params, data) {
    if ($.trim(params.term) === '') {
      return null;
    }

    if (typeof data.text === 'undefined') {
      return null;
    }

    if (data.text === params.term) {
      return $.extend({}, data, true);
    }

    return null;
  }
});
