window.utils.$document.ready(() => {
    const datetimepicker = $('.date-picker');

    datetimepicker.each((index, element) => {
        const $this = $(element);
        
        const options = $.extend({
                altInput: true,
                altFormat: "F j, Y",
                dateFormat: "Y-m-d",
                disableMobile: true
            },
            $this.data('options')
        );
        
        $this.attr('placeholder', options.dateFormat);
        
        $this.flatpickr(options);
    });
});