"use strict";

var DataTable = $.fn.dataTable;

$.extend(true, DataTable.Buttons.defaults, {
	dom: {
		container: {
			className: 'dt-buttons'
		},
		button: {
			className: 'btn'
		}
	},
	buttonCreated: function ( config, button ) {
		return button;
	}
});

$.extend(true, $.fn.dataTable.defaults, {
	paging: false,
	searching: true,
	processing: true,
	dom: "rt",
	order: [[0, 'asc']],
	autoWidth: false,
	orderCellsTop: true,
	language: {
		processing: '<div class="table-processing-content"><div class="spinner-border text-primary" role="status"></div></div>'
	},
	classes: {
		sProcessing: 'table-processing'
	},
	createdRow: function (row, data, dataIndex, cells) {
		const table = this;
		const tableApi = table.api();
		const settings = table.fnSettings();
		const $row = $(row);

		const rowClasses = DataTable.parseClasses(settings.oClasses.cRow, row, data) || [];
		$row.addClass(rowClasses);

		const cCol = table.fnSettings().oClasses.cCol || {};
		const colsClasses = Object.keys(cCol).map(key => ({
			name: key.endsWith('Col') ? key : `${key}Col`,
			classes: DataTable.parseClasses(cCol[key], row, data)
		}));

		colsClasses.forEach(({ name, classes }) => {
			const aoColumn = settings.aoColumns.find(_ => _.data === name);
			if (!aoColumn) {
				return;
			}

			$('td', row).eq(aoColumn.idx).addClass(classes);
		});

		$('[dt-if]', row)
			.each(function () {
				const $elem = $(this);
				const isVisibleExpression = $elem.attr('dt-if');
				const isVisible = eval(isVisibleExpression);
				
				if (isVisible) {
					$elem.show();
				} else {
					$elem.hide();
				}
			});
	},
	initComplete: function () {
		var table = this;
		var tableApi = table.api();

		var columns = [];
		tableApi.columns().every(function (index) { columns[index] = this; });
		
		columns
			.filter(function (column, index) {
				return column.visible();
			})
			.forEach(function (column, index) {
				$(`thead tr:eq(1) th:eq(${index}) input[type=text].searching`, table)
					.on('keyup change', function () {
						if (column.search() !== this.value) {
							column.search(this.value);
							column.draw();
						}
					});

				$(`thead tr:eq(1) th:eq(${index}) select.searching`, table)
					.on('change', function () {
						if (column.search() !== this.value) {
							column.search(this.value);
							column.draw();
						}
					});
			});

		$('.delete-action', table).on('click', function() {
			var data = $(this).data();

			var modal$ = $('#global-delete-modal');

			modal$.find('form').attr('action', data.deleteUrl);

			modal$.modal('show');
		});

		$('.btn-details', table).on('click', function () {
			var button = $(this);
			var url = button.data().url;

			var tr = button.closest('tr');
			var row = tableApi.row(tr);

			if (row.child.isShown()) {
				row.child.hide();
				button.removeClass('active');
			} else {
				tableApi.processing(true);

				$.ajax({
					url: url,
					type: "GET",
					success: function (result) {
						row
							.child(result, "details")
							.show();

						button.addClass('active');
					},
					complete: function () {
						tableApi.processing(false);
					}
				});
			}
		});
	}
});

DataTable.onExportExcelActionTable = function (e, dt, button, config) {
	var self = this;
	var oldStart = dt.settings()[0]._iDisplayStart;

	dt.one('preXhr', function (e, s, data) {
		// Just this once, load all data from the server...
		data.start = 0;
		data.length = 2147483647;

		dt.one('preDraw', function (e, settings) {
			// Call the original action function
			if (button[0].className.indexOf('buttons-excel') >= 0) {
				$.fn.dataTable.ext.buttons.excelHtml5.available(dt, config) ?
					$.fn.dataTable.ext.buttons.excelHtml5.action.call(self, e, dt, button, config) :
					$.fn.dataTable.ext.buttons.excelFlash.action.call(self, e, dt, button, config);
			}

			dt.one('preXhr', function (e, s, data) {
				// DataTables thinks the first item displayed is index 0, but we're not drawing that.
				// Set the property to what it was before exporting.
				settings._iDisplayStart = oldStart;
				data.start = oldStart;
			});

			// Reload the grid with the original page. Otherwise, API functions like table.cell(this) don't work properly.
			setTimeout(dt.ajax.reload, 0);

			// Prevent rendering of the full data to the DOM
			return false;
		});
	});

	// Requery the server with the new one-time export settings
	dt.ajax.reload();
}

DataTable.onRenderIndexColumnTable = function (data, type, row, meta) {
	return meta.row + 1;
}

DataTable.onRenderImageColumnTable = function (data, type, row, meta) {
	return (!!data && data !== '-')
		? `<img style="width: 50px; max-height: 50px;" src="/document/${data}" />`
		: '-';
}

DataTable.parseClasses = function (input, row, data) {
	if (!input) {
		return null;
	}
	
	input = typeof input === 'function'
		? input(row, data)
		: input;

	if (!input) {
		return null;
	}

	if (typeof input === 'string') {
		return [input];
	}

	if (input instanceof Array) {
		return input.filter(_ => !!_ && typeof _ === 'string');
	}

	const keys = Object.keys(input);

	return keys.filter(_ => !!input[_]);
}
