(function ($) {
    if($.validator && $.validator.unobtrusive){
        const highlight = $.validator.defaults.highlight;
        const unhighlight = $.validator.defaults.unhighlight;

        $.validator.setDefaults({
            highlight: function (element, errorClass, validClass) {
                const $parent = $(element).parent();
                if ($parent.is('.select2')) {
                    element = $parent.next().find('.select2-selection');
                }

                highlight.call(this, element, errorClass, validClass);
            },
            unhighlight: function (element, errorClass, validClass) {
                const $parent = $(element).parent();
                if ($parent.is('.select2')) {
                    element = $parent.next().find('.select2-selection');
                }

                unhighlight.call(this, element, errorClass, validClass);
            }
        });

        const errorPlacement = $.validator.unobtrusive.options.errorPlacement;

        $.validator.unobtrusive.options = {
            ...$.validator.unobtrusive.options,
            errorPlacement: function (error, element) {
                const $target = $(element).next();
                if ($target.is(".select2")) {
                    element = $target;
                }

                errorPlacement.call(this, error, element);
            }
        };

        $(document).on('change', '.select2', function() {
            $(this).valid();
        });
    }
    else {
        console.warn('$.validator is not defined. Please load this library **after** loading jquery.validate.js and jquery.validate.unobtrusive.js');
    }
})(jQuery);

