"use strict";

import utils from "../utils";

utils.$document.ready(() => {
    const $checkListCategoryIdFormControl = $(".check-list-category-form-control");
    const $checkListItemAliasFormControl = $(".check-list-item-alias-form-control");
    const $defaultCheckListItemCategoryAliases = $('option', $checkListItemAliasFormControl).map(function () {
        return +this.value;    
    });
    const defaultCheckListCategoryAliases = [...$defaultCheckListItemCategoryAliases];

    $checkListCategoryIdFormControl
        .change(function(){
            const checkListCategoryId = $(this).val();

            if (!checkListCategoryId) {
                $checkListItemAliasFormControl.prop('disabled', true);
                $(this).val(0);
                return;
            }

            $.ajax({
                url: `/check-list-item/category/${checkListCategoryId}/alias-available.json`,
                type: "GET",
                success: function(availableCheckListItemAliases) {
                    $checkListItemAliasFormControl.prop('disabled', false);
                    setCheckListItemAliasesVisibility($checkListItemAliasFormControl, availableCheckListItemAliases);
                }
                
            });  
        })
        .change();

        function setCheckListItemAliasesVisibility($element, availableCheckListItemAliases) {

            $('option', $element).each(function () {
                
               if (availableCheckListItemAliases.includes(+this.value)) {
                    $(this).show();
                } else {
                    $(this).hide();
                }
            });
            $element.val(availableCheckListItemAliases[0])
        }
});