"use strict";

import utils from "../utils";

utils.$document.ready(() => {
    const $brandFormControl = $(".brand-form-control");
    const $dealerFormControl = $('.dealer-form-control');

    $brandFormControl.trigger('reset');
    $dealerFormControl.trigger('reset');

    $brandFormControl
        .ready(function() {
            $.ajax({
                url: '/brand/available.json',
                type: "GET",
                success: function(brands) {
                    const options = brands.map(_ => `<option value="${_.id}" data-deactivated="${_.isDeactivated}">${_.name}</option>`);
                    const brandId = $brandFormControl.val();

                    clearOptions($brandFormControl);
                    
                    $brandFormControl.append(options.join(''));
                    $brandFormControl.val(brandId);
                    $brandFormControl.trigger('reset');
                }
            });
        })
        .change(function() {
            const brandId = $brandFormControl.val();
            if (!brandId) {
                clearOptions($dealerFormControl);
                $dealerFormControl.val(null);
                $dealerFormControl.trigger('reset');

                return;
            }

            $.ajax({
                url: `/brand/${brandId}/dealer/available.json`,
                type: "GET",
                success: function(dealers) {
                    const options = dealers.map(_ => `<option value="${_.id}" data-deactivated="${_.isDeactivated}">${_.name}</option>`);
                    const dealerId = $dealerFormControl.val();

                    clearOptions($dealerFormControl);

                    $dealerFormControl.append(options.join(''));
                    $dealerFormControl.val(dealerId);
                    $dealerFormControl.trigger('reset');
                }
            });
        })
        .change();

    function clearOptions($element) {
        $('option', $element)
            .filter(function () {
                return !!this.value;
            })
            .remove();
    }
});
