"use strict";

import utils from "../utils";

utils.$document.ready(() => {
    const $brandUserAccessFormControl = $(".brand-user-access-form-control");
    const $userFormControl = $('.user-form-control');

    $brandUserAccessFormControl.trigger('reset');

    $userFormControl
        .change(function() {
            const userId = $userFormControl.val();
            if (!userId) {
                $brandUserAccessFormControl.val(null);
                $brandUserAccessFormControl.trigger('change');

                return;
            }
            
            $.ajax({
                url: `/user-access/user/${userId}/brand/accesses.json`,
                type: "GET",
                success: function(brandIds) {
                    $brandUserAccessFormControl.val(brandIds);
                    $brandUserAccessFormControl.trigger('change');
                }
            });
        })
        .change();
});
