"use strict";

import utils from "../utils";

utils.$document.ready(() => {
    const $dealerUserAccessFormControl = $(".dealer-user-access-form-control");
    const $userFormControl = $('.user-form-control');

    $dealerUserAccessFormControl.trigger('reset');

    $userFormControl
        .change(function() {
            const userId = $userFormControl.val();
            if (!userId) {
                $dealerUserAccessFormControl.val(null);
                $dealerUserAccessFormControl.trigger('change');

                return;
            }
            
            $.ajax({
                url: `/user-access/user/${userId}/dealer/accesses.json`,
                type: "GET",
                success: function(dealerIds) {
                    $dealerUserAccessFormControl.val(dealerIds);
                    $dealerUserAccessFormControl.trigger('change');
                }
            });
        })
        .change();
});
