"use strict";

import utils from "../utils";

utils.$document.ready(() => {
    const BreakageImageType = window.BreakageImageType;

    const $brandFormControl = $(".brand-form-control");
    const $dealerFormControl = $('.dealer-form-control');
    const $breakageImageTypeFormControl = $(".breakage-image-type-form-control");

    if ($dealerFormControl.length > 0) {
        $dealerFormControl
            .change(function() {
                const brandId = $brandFormControl.val();
                const dealerId = $dealerFormControl.val();

                if (!brandId || !dealerId) {
                    setDefaultTypeVisibility($breakageImageTypeFormControl, false);

                    return;
                }

                $.ajax({
                    url: `/brand/${brandId}/dealer/${dealerId}/dealer-breakage-image/default-type-available.json`,
                    type: "GET",
                    success: function(isAvailable) {
                        setDefaultTypeVisibility($breakageImageTypeFormControl, isAvailable);
                    }
                });
            })
            .change();
    } else {
        $brandFormControl
            .change(function() {
                const brandId = $brandFormControl.val();

                if (!brandId) {
                    setDefaultTypeVisibility($breakageImageTypeFormControl, false);

                    return;
                }

                $.ajax({
                    url: `/brand/${brandId}/brand-breakage-image/default-type-available.json`,
                    type: "GET",
                    success: function(isAvailable) {
                        setDefaultTypeVisibility($breakageImageTypeFormControl, isAvailable);
                    }
                });
            })
            .change();
    }

    function setDefaultTypeVisibility($element, visible) {
        const $defaultTypeOption = $('option', $element)
            .filter(function () {
                return this.value == BreakageImageType.Default;
            })
            .first();

        if (visible) {
            $defaultTypeOption.show();
        } else {
            $defaultTypeOption.hide();
        }
    }
});
