"use strict";

import utils from "../utils";

utils.$document.ready(() => {
    const $breakageImageTypeFormControl = $(".breakage-image-type-form-control");
    const $breakageImageMaskFormControl = $(".breakage-image-mask-form-control");

    $breakageImageTypeFormControl
        .change(function() {
            const value = $(this).val();
            const disabled = value == BreakageImageType.Default;

            $breakageImageMaskFormControl.prop('disabled', disabled);
            
            if (disabled) {
                $breakageImageMaskFormControl.valid();
            }
        })
        .change();
});
