"use strict";

import utils from "../utils";

utils.$document.ready(() => {
    const $brandFormControl = $(".brand-form-control");
    const $dealerFormControl = $('.dealer-form-control');
    const $departmentFormControl = $('.department-type-form-control');
    const $checkListCategoryAliasFormControl = $(".check-list-category-alias-form-control");
    const $defaultCheckListCategoryAliases = $('option', $checkListCategoryAliasFormControl).map(function () {
        return +this.value;    
    });
    const defaultCheckListCategoryAliases = [...$defaultCheckListCategoryAliases];
   
    if ($dealerFormControl.length > 0) {
        $dealerFormControl
            .change(function() {
                const brandId = $brandFormControl.val();
                const dealerId = $dealerFormControl.val();
                const departmentType = $departmentFormControl.val();
      
                if (!brandId || !dealerId) {
                    setCheckListAliasesVisibility($checkListCategoryAliasFormControl, []);

                    return;
                }

    
                $.ajax({
                    url: `/brand/${brandId}/dealer/${dealerId}/check-list-category/dealer-alias-unavailable.json?departmentType=${departmentType}`,
                    type: "GET",
                    success: function(unavailableCheckListCategoryAliases) {
                      
                        const availableCheckListCategoryAliases = defaultCheckListCategoryAliases                
                            .filter(checkListCategoryAlias => !unavailableCheckListCategoryAliases.includes(checkListCategoryAlias));

                        setCheckListAliasesVisibility($checkListCategoryAliasFormControl, availableCheckListCategoryAliases);
                      }
                    
                });
                
            })
            .change();
    } else {
        $brandFormControl
            .change(function() {
                const brandId = $brandFormControl.val();
                const departmentType = $departmentFormControl.val();

                if (!brandId) {
                    setCheckListAliasesVisibility($checkListCategoryAliasFormControl, []);

                    return;
                }
                $.ajax({
                    url: `/brand/${brandId}/check-list-category-preset/brand-alias-unavailable.json?departmentType=${departmentType}`,
                    type: "GET",
                    success: function(unavailableCheckListCategoryAliases) {
                      
                        const availableCheckListCategoryAliases = defaultCheckListCategoryAliases                
                            .filter(checkListCategoryAlias => !unavailableCheckListCategoryAliases.includes(checkListCategoryAlias));

                        setCheckListAliasesVisibility($checkListCategoryAliasFormControl, availableCheckListCategoryAliases);
                    }
                });
            })
            .change();
    }

    function setCheckListAliasesVisibility($element, availableCheckListCategoryAliases) {

        $('option', $element).each(function () {
      
           if (availableCheckListCategoryAliases.includes(+this.value)) {
                $(this).show();
            } else {
                $(this).hide();
            }
        });
    }
});