"use strict";

/*-----------------------------------------------
|   Utilities
-----------------------------------------------*/
const utils = (($) => {
  const Utils = {
    $window: $(window),
    $document: $(document),
    $html: $("html"),
    $body: $("body"),
    $main: $("main"),
    
    location: window.location,
    nua: navigator.userAgent,
    navigate(url, ...urlArgs) {
      this.location.href = formatStr(url, ...urlArgs);
    },

    isIterableArray(array) {
      return Array.isArray(array) && !!array.length;
    },

    setCookie(name, value, expire) {
      const expires = new Date();
      expires.setTime(expires.getTime() + expire);
      document.cookie =
        name + "=" + value + ";expires=" + expires.toUTCString();
    },
    getCookie(name) {
      var keyValue = document.cookie.match("(^|;) ?" + name + "=([^;]*)(;|$)");
      return keyValue ? keyValue[2] : keyValue;
    },

    setSpinner(value) {
      if (value) {
        $('#global-spinner').show();
      } else {
        $('#global-spinner').hide();
      }
    },

    openNameConfirmActionModal(url, action, name) {  
      var modal$ = $('#global-name-confirm-action-modal');

      var actionHint$ = modal$.find('.action-hint');
      var originalName$ = modal$.find('.original-name');
      
      var template = actionHint$.data().template;
      var actionHint = formatStr(template, action, name);
      actionHint$.text(actionHint);

      originalName$.val(name);

      modal$.find('form').attr('action', url);

      modal$.modal('show');
    },
    
    breakpoints: {
      xs: 0,
      sm: 576,
      md: 768,
      lg: 992,
      xl: 1200,
      xxl: 1540,
    },
    getBreakpoint($element) {
      const classes = $element.attr("class");
      let breakpoint;
      if (classes) {
        breakpoint = this.breakpoints[
          classes
            .split(" ")
            .filter((cls) => cls.indexOf("navbar-expand-") === 0)
            .pop()
            .split("-")
            .pop()
        ];
      }
      return breakpoint;
    },

  };

  function formatStr(str, ...args) {
    return str.replace(/{(\d+)}/g, function(match, number) { 
      return typeof args[number] != 'undefined'
        ? args[number]
        : match
      ;
    });
  };

  return Utils;
})(jQuery);

export default utils;
