"use strict";

import utils from "../utils";

utils.$document.ready(() => {
    const $dealerManagementSystemFormControl = $(".dealer-management-system-form-control");

    $dealerManagementSystemFormControl.trigger('reset');

    $dealerManagementSystemFormControl
        .ready(function() {
            $.ajax({
                url: '/dms/available.json',
                type: "GET",
                success: function(dealerManagementSystems) {
                    const options = dealerManagementSystems.map(_ => `<option value="${_.id}">${_.name}</option>`);

                    clearOptions($dealerManagementSystemFormControl);

                    $dealerManagementSystemFormControl.append(options.join(''));
                    $dealerManagementSystemFormControl.trigger('reset');
                }
            });
        })
        .change();

    function clearOptions($element) {
        $('option', $element)
            .filter(function () {
                return !!this.value;
            })
            .remove();
    }
});