"use strict";

import utils from "../utils";

utils.$document.ready(() => {
    const $typeFormControl = $(".analytics-filter-type-form-control");
    const $userFormControl = $(".analytics-filter-user-form-control");

    $typeFormControl
        .change(function () {
            var value = $(this).val();

            if (value == AnalyticsFilterType.ForUser) {
                $userFormControl.prop('disabled', false);
            } else {
                $userFormControl.prop('disabled', true);
            }
        })
        .change();
});