window.utils.$document.ready(() =>{
    const $qrCode = $('[data-qr-code]');

    $qrCode.each((index, value) => {
        const $this = $(value);
        const options = $this.data('qr-code');

        QRCode.toCanvas(value, options.qrCodeUri, { errorCorrectionLevel: options.errorCorrectionLevel})
    });
});